// ELEMENTS

.contain {
  max-width: #{$global-max-page-width}px;
  width: 100%;
  margin: 0 auto;
}

.contain-thin {
  max-width: 720px;
  width: 100%;
  margin: 0 auto;
}

.contain-header {
  max-width: calc(#{$global-max-page-width}px + 80px);
  width: 100%;
  margin: 0 auto;
}
