//================================================================================================//
//
//  GLOBAL CALCULATIONS
//  N.B: Functions NEVER output CSS
//
//================================================================================================//

// FUNCTIONS

@function calc-col-width($m , $c, $n) {
  $x: (($m - 1) / $c);
  @return (100 * ($x / $m)) * $n;
}
