// ANIMATIONS

[data-animation-enter] {
  @include animate(opacity transform, 250ms, $easing, 50ms);
  will-change: opacity, transform;
  opacity: 0;

  &.animate {
    opacity: 0;
    transform: translateY(10px);
  }

  &.animate-in {
    transition: opacity 500ms, transform 250ms;
    opacity: 1;
    transform: translateY(0);
  }

  .no-js &,
  .is-ie & {
    opacity: 1;
    transform: translateY(0);
  }
}
